import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JapanseGrammarComponent } from './page/japanse-grammar/japanse-grammar.component';
import { MemriseComponent } from './page/memrise/memrise.component';

const routes: Routes = [
  { path: '', component: JapanseGrammarComponent },
  { path: 'memrise', component: MemriseComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
