import { Component, OnInit, HostListener } from '@angular/core';
import data from '../../../assets/data/DataJsonJlptVietNam.json';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-memrise',
  templateUrl: './memrise.component.html',
  styleUrls: ['./memrise.component.css']
})
export class MemriseComponent implements OnInit {

  constructor(private translate: TranslateService) {
    translate.setDefaultLang('vn');
  }

  levelList = ['All', 'N5', 'N4', 'N3', 'N2', 'N1'];
  selectedLevel = '';
  filter = true;
  realMode = false;
  multiLanguge = { vn: false, ja: false, en: false };
  keySearch = '';

  // tslint:disable-next-line: no-use-before-declare
  listData = [];
  listDataLevel = {
    All: [],
    N1: [],
    N2: [],
    N3: [],
    N4: [],
    N5: []
  };

  isShow: boolean;
  topPosToStartShowing = 100;

  ngOnInit() {
    let ii = 1;
    listDataConst.forEach(element => {
      element['lesson'] = ii++;
    });

    this.multiLanguge.vn = true;
    this.listDataLevel.All = listDataConst;
    this.listDataLevel.N1 = listDataConst.filter(item => item.level === 'N1');
    this.listDataLevel.N2 = listDataConst.filter(item => item.level === 'N2');
    this.listDataLevel.N3 = listDataConst.filter(item => item.level === 'N3');
    this.listDataLevel.N4 = listDataConst.filter(item => item.level === 'N4');
    this.listDataLevel.N5 = listDataConst.filter(item => item.level === 'N5');
  }

  openFilter() {
    if (this.filter === true) {
      this.filter = false;
    } else {
      this.filter = true;
    }
  }

  changeMode() {
    if (this.realMode === true) {
      this.realMode = false;
    } else {
      this.realMode = true;
    }
  }

  changeLanguge(name: any) {
    this.multiLanguge.vn = false;
    this.multiLanguge.ja = false;
    this.multiLanguge.en = false;

    if (name === 'vn') {
      this.multiLanguge.vn = true;
      this.translate.setDefaultLang('vn');
    } else if (name === 'ja') {
      this.multiLanguge.ja = true;
      this.translate.setDefaultLang('ja');
    } else if (name === 'en') {
      this.multiLanguge.en = true;
      this.translate.setDefaultLang('en');
    }
  }

  search(event: any) {

    this.changeLevel(this.selectedLevel);

    if (event.target.value !== '') {

      this.changeLevel(this.selectedLevel);

      this.listData = this.listData.filter(item =>
        item.contentResearch.toLowerCase().indexOf(this.replaceKey(event.target.value).toLowerCase()) > -1
      );
    }

  }

  plusMeaningUse(listString: any[]) {
    let result = '';
    listString.forEach(element => {
      result += element.value + ' ';
    });
    return result;
  }

  plusSentence(listString: any[]) {
    let result = '';
    listString.forEach(element => {
      result += element.reibunJA + ' ' + element.reibunVN + ' ' + element.reibunRO;
    });
    return result;
  }

  changeLevel(event: any) {
    this.selectedLevel = event;
    if (this.selectedLevel === 'All') {
      this.listData = this.listDataLevel.All;
    } else if (this.selectedLevel === 'N1') {
      this.listData = this.listDataLevel.N1;
    } else if (this.selectedLevel === 'N2') {
      this.listData = this.listDataLevel.N2;
    } else if (this.selectedLevel === 'N3') {
      this.listData = this.listDataLevel.N3;
    } else if (this.selectedLevel === 'N4') {
      this.listData = this.listDataLevel.N4;
    } else if (this.selectedLevel === 'N5') {
      this.listData = this.listDataLevel.N5;
    } else {
      this.listData = [];
    }
  }

  @HostListener('window:scroll')
  checkScroll() {
    const scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;

    if (scrollPosition >= this.topPosToStartShowing) {
      this.isShow = true;
    } else {
      this.isShow = false;
    }
  }

  gotoTop() {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }

  replaceKey(key: string) {
    let i;
    let spaceCount = (key.split(' ').length);
    if (spaceCount <= 15) {
      spaceCount = 15;
    }
    for (i = 0; i <= spaceCount; i++) {
      key = key.replace('\n', '')
        .replace('…', '')
        .replace('\r', '')
        .replace('『', '')
        .replace('』', '')
        .replace('.', '')
        .replace('✙', '')
        .replace('　✙　', '')
        .replace('。', '')
        .replace(' ', '')
        .replace('/', '')
        .replace('(', '')
        .replace(')', '')
        .replace('　', '')
        .replace('“', '')
        .replace('”', '')
        .replace('～', '')
        .replace('~', '')
        .replace(' (', '')
        .replace('？', '')
        .replace('?', '')
        .replace('!', '')
        .replace('+', '')
        .replace('-', '')
        .replace('"', '');
    }
    return key;
  }

}

const listDataConst = data;
